
body {
  font-family: 'Open Sans', sans-serif;
}

div#layout {
  text-align: center;
}

div#container {
  width: 700px;
  height: 500px;
  margin: auto;
  /* background-color: #2F4A6D; */
}

svg {
  width: 100%;
  height: 100%;
}

.bar {
  /* fill: #80cbc4; */
}


path {
  stroke: gray;
}

line {
  stroke: gray;
}

line#limit {
  stroke: #FED966;
  stroke-width: 5;
  stroke-dasharray: 5 8;
}

.grid path {
  stroke-width: 0;
}

.grid .tick line {
  stroke: #9FAAAE;
  stroke-opacity: 0.5;
}

text {
  font-size: 25px;
  fill: black;
}

text.divergence {
  font-size: 30px;
  fill: #2F4A6D;
}

text.value {
  font-size: 30px;
  font-weight: 600;
}

text.title {
  font-size: 30px;
  font-weight: 600;
}

text.label {
  font-size: 40px;
  font-weight: 400;
}

text.source {
  font-size: 18px;
}